* {
  box-sizing: border-box;
}

@font-face {
  font-family: Avenir;
  font-weight: 300;
  src: url(fonts/Avenir.ttc);
}

@font-face {
  font-family: Avenir;
  font-weight: 400;
  src: url(fonts/Avenir-Book.ttf);
}

@font-face {
  font-family: Avenir;
  font-weight: 900;
  src: url(fonts/Avenir-Heavy.ttf);
}

.form {
  width: 100%;
  position: relative;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61,78,97,0.1);
  border-radius: 4px;
  background-color: #fff;
  margin-top: 20px;
}

.form:hover {
  box-shadow: 0 3px 8px rgba(61,78,97,0.2);
}

.form-field {
  display: block;
  width: 100%;
}

.form-field-big {
  height: 68px;
}

.form-field-small {
  height: 40px;
}

.field {
  font-family: Avenir, sans-serif;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
}

.field:focus {
  font-weight: 900;
}

.field::placeholder {
  font-family: Avenir, sans-serif;
  font-weight: 400;
}

.field-big {
  font-size: 16px;
  padding-left: 62px;
}

.field-big::placeholder {
  font-size: 16px;
}

.field-small {
  font-size: 14px;
  font-weight: 400;
  padding-left: 33px;
}

.field-small::placeholder {
  font-size: 14px;
}

.form-search-button {
  height: 100%;
  position: absolute;
  top: -1px;
  left: 1px;
  background-color: transparent;
  border: none;
  outline: none;
  background-image: url(images/Search.svg);
  background-repeat: no-repeat;
  background-position: 50%;
  cursor: pointer;
}

.form-search-button-big {
  width: 62px;
  background-size: 19px;
}

.form-search-button-small {
  width: 33px;
  background-size: 11px;
}
